#how-it-works__img {
  min-height: 350px;
  background-image: url("images/How It Works.png");
  background-repeat: no-repeat;
  display: block;
  margin: auto;
  background-position: center;
  background-size: contain;
}

@media screen and (max-width: 769px) {
  #placeholders {
    margin-top: 100px;
  }
  #how-it-works__img {
    background-image: url("images/How It Works Mobile.png");
    width: 100%;
    min-height: 800px;
  }
  .why-it-works__img {
    margin-top: 3em;
    width: 100%;
    height: initial !important;
  }
  .hero__img {
    width: 400px;
  }
  .dialog {
    margin: 0 !important;
  }
}

@media screen and (max-height: 500px) {
  .dialog {
    height: 100%;
    overflow: auto;
    min-height: initial;
  }
}

@media screen and (max-height: 300px) {
  .dialog {
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    min-height: initial !important;
  }
}

@media screen and (max-width: 600px) {
  .dialog {
    width: 100% !important;
  }
}

