    margin-top: 3em;
    width: 100%;
    height: auto !important;
    height: initial !important;
  }
  .hero__img {
    width: 400px;
  }
  .dialog {
    margin: 0 !important;
  }
}

@media screen and (max-height: 500px) {
  .dialog {
    height: 100%;
    overflow: auto;
    min-height: 0;
    min-height: initial;
