  .dialog {
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    min-height: 0 !important;
    min-height: initial !important;
  }
}

@media screen and (max-width: 600px) {
  .dialog {
    width: 100% !important;
  }
}

